body{
    background-color: #F2F2F2;
    color: #4F4F4F;
}

body img{
    border-radius: 12px;
}
.container{
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    gap: 3vh;
}

.BasicInfoDiv, .skillDiv, .projectsDiv--titileDiv, .hobbiesDiv, .projectElementsDiv{
    background-color: #FFFFFF;
    border-radius: 12px;

    -webkit-box-shadow: 2px 2px 15px -12px rgba(66, 68, 90, 1);
    -moz-box-shadow: 2px 2px 15px -12px rgba(66, 68, 90, 1);
    box-shadow: 2px 2px 15px -12px rgba(66, 68, 90, 1);
}

.gridContainer{
    margin-left: auto;
    margin-right: auto;

    width: 95%;
    max-width: 1000px;


    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
}

h1{
    font-size: 20px;
    font-weight: 600;
}

h2{
    font-size: 16px;
    font-weight: bold;
}

h3{
    font-size: 14px;
}

h4{
    font-weight: 500;
    color: #828282;
    font-size: 12px;
}

a{
    color: #828282;
}

p{
    color: #828282;
    font-size: 12px;
}


